body.login h1 a{
  background-image: url("../img/dog-blue.png");
  border-radius: 50%;
}
.cappy-login-notice{
  padding: 10px;
  margin: 20px 0;
  text-align: center;
  background:  #21759B;
  color: #fff;
  border-radius: 2px;
  position: relative;
  &:before{
    content: "";
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-bottom-color: #21759B;
    left: 50%;
    top: 0;
    transform: translate(-50%, -100%);
  }
}
